'use client';

import React, { createContext, useContext, useState, useEffect } from 'react';
import { useAuth } from './AuthContext';
import { CompanyService } from '@/lib/db/services';

interface SubscriptionInfo {
  tier: 'free' | 'pro' | 'premium';
  contactsUsed: number;
  contactsLimit: number;
  expiresAt?: Date;
}

interface SubscriptionContextType {
  subscription: SubscriptionInfo;
  canContact: boolean;
  recordContact: (driverId: string, contactType: string) => Promise<boolean>;
  upgradeSubscription: (tier: 'pro' | 'premium') => Promise<boolean>;
  refreshSubscription: () => Promise<void>;
  loading: boolean;
}

const SubscriptionContext = createContext<SubscriptionContextType | undefined>(undefined);

export function SubscriptionProvider({ children }: { children: React.ReactNode }) {
  const { user } = useAuth();
  const [subscription, setSubscription] = useState<SubscriptionInfo>({
    tier: 'free',
    contactsUsed: 0,
    contactsLimit: 10,
  });
  const [loading, setLoading] = useState(false);

  const refreshSubscription = async () => {
    if (!user || user.role !== 'company') return;

    try {
      setLoading(true);
      const subscriptionInfo = await CompanyService.getSubscriptionInfo(user.id);
      setSubscription({
        tier: subscriptionInfo.tier as 'free' | 'pro' | 'premium',
        contactsUsed: subscriptionInfo.contactsUsed,
        contactsLimit: subscriptionInfo.contactsLimit,
        expiresAt: subscriptionInfo.expiresAt || undefined,
      });
    } catch (error) {
      console.error('Error fetching subscription info:', error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    refreshSubscription();
  }, [user]);

  const canContact = subscription.contactsUsed < subscription.contactsLimit;

  const recordContact = async (driverId: string, contactType: string): Promise<boolean> => {
    if (!user || user.role !== 'company') return false;
    if (!canContact) return false;

    try {
      await CompanyService.recordContact(user.id, driverId, contactType);
      await refreshSubscription(); // Refresh to get updated counts
      return true;
    } catch (error) {
      console.error('Error recording contact:', error);
      return false;
    }
  };

  const upgradeSubscription = async (tier: 'pro' | 'premium'): Promise<boolean> => {
    if (!user || user.role !== 'company') return false;

    try {
      setLoading(true);
      await CompanyService.upgradeSubscription(user.id, tier);
      await refreshSubscription(); // Refresh to get updated subscription
      return true;
    } catch (error) {
      console.error('Error upgrading subscription:', error);
      return false;
    } finally {
      setLoading(false);
    }
  };

  return (
    <SubscriptionContext.Provider value={{
      subscription,
      canContact,
      recordContact,
      upgradeSubscription,
      refreshSubscription,
      loading,
    }}>
      {children}
    </SubscriptionContext.Provider>
  );
}

export function useSubscription() {
  const context = useContext(SubscriptionContext);
  if (context === undefined) {
    throw new Error('useSubscription must be used within a SubscriptionProvider');
  }
  return context;
}
